.error-404 {
    width: 100%;
    height: 500px;
    padding-top: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: $ghost-white;

    &__title {
        font-size: 8rem;
    }

    &__subtitle {
        font-size: 3rem;
    }

    &__spacer {
        width: 10%;
        height: 1px;
        background-color: $masonic-pink;
        margin: 20px 0;
    }

    &__text {
        font-size: 1.5rem;
    }
}